<script>
import {navModules} from "@/modules";

export default {
  data() {
    return {
      navModules,
      VUE_APP_VERSION: process.env.VUE_APP_VERSION,
    };
  },

  computed: {
    sortedNav() {
      const list = [ ...this.navModules ];
      return list.sort((a, b) => a.title.localeCompare(b.title));
    },
  },

  created() {
    console.log(process.env);
  }
}
